* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

img {
  display: block;

}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul{
  margin: 0;
  padding: 0;
}

.country-item{
  display: flex;
  align-items: center;

}

.country-name {
  margin-left: 10px;
}